.eg-btn {
  display: inline-block;
  height: 50px;
  color: $white;
  font-size: 20px;
  line-height: 24px;
  font-weight: 300;
  padding: 12px 30px 14px 30px;
  border-radius: 25px;
  border: none;
  cursor: pointer;

  &--orange {
    background: $light-red;
    color: $white;
    transition: opacity ease-in 0.15s;

    &:hover {
      opacity: 0.9;
    }
  }
}
